@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#header { width: 100%; }
#header .inner { height: 120px; }
#header h1 { position: absolute; left: 0; top: 35px; }
#header .all-menu { position: absolute; right: 0; top: 30px; cursor: pointer; }
#header .all-menu i { font-size: 35px; }

.swiper-container .swiper-slide  h2 {
    position: absolute; left: 50%; font-size: 60px;
    top: 40%; transform: translate(-50%,-50%);
    color:#fff; 
    letter-spacing: -2px;
    text-align: center;
    line-height: 1.3;
    font-weight: 600;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
    font-family: 'Noto Sans KR', sans-serif;
	line-height: 1.6;
    color:#333;
    font-size: 15px;
}
a {
    font-family: 'Noto Sans KR', sans-serif;
	line-height: 1.6;
    color:#333;
    font-size: 15px;
    text-decoration: none;
}

ol, ul,li {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
#wrap { width: 100%; }
.inner { width: 1400px; margin: auto; position: relative; }

#content { width: 100%; }
#content .inner {  }
#content h2 { text-align: center; padding: 45px 0; font-size: 40px; font-weight: 600; letter-spacing: -1px; }

.list-menu { display: flex;  justify-content: space-between; border-top: 1px solid #dcdcdc; border-bottom: 1px solid #dcdcdc; margin-bottom: 50px; }
.list-menu li { font-size: 20px; color:#555; font-weight: 500; line-height: 70px; padding: 0 30px; }

.card-list { display: flex; flex-wrap: wrap; justify-content: space-between; }
.card-list li { width: 430px; margin-bottom: 50px; }
.card-list li img { width: 100%; }

.more { width: 70px; margin: auto; text-align: center; cursor: pointer; }
.more i { font-size: 50px; }
.more span { display: block; margin-top: -20px; font-size: 16px; }

#footer { background: #303030; margin-top: 100px; padding: 80px 0; text-align: center; color:#dcdcdc; position: relative; }
#footer::before, #footer::after { content:""; display: block; position: absolute; width: 28%; height: 1px; background: #dcdcdc; opacity: 0.3; top: 170px; }
#footer::before { left: 0; }
#footer::after { right: 0; }

#footer .inner {  }
#footer h2 { margin-bottom: 30px; }

#footer .bottom-menu { margin-bottom: 30px; }
#footer .bottom-menu a { color:#dcdcdc; margin-right: 20px; font-size: 16px; }
#footer .bottom-menu a::after { content:""; display: inline-block; width: 5px; height: 5px; border-radius: 50%; background: #dcdcdc; margin-bottom: 3px; margin-left: 20px; }
#footer .bottom-menu a:last-child { margin-right: 0; }
#footer .bottom-menu a:last-child::after { display: none; }

#footer p {  }
