#content { width: 100%; }
#content .inner {  }
#content h2 { text-align: center; padding: 45px 0; font-size: 40px; font-weight: 600; letter-spacing: -1px; }

.list-menu { display: flex;  justify-content: space-between; border-top: 1px solid #dcdcdc; border-bottom: 1px solid #dcdcdc; margin-bottom: 50px; }
.list-menu li { font-size: 20px; color:#555; font-weight: 500; line-height: 70px; padding: 0 30px; }

.card-list { display: flex; flex-wrap: wrap; justify-content: space-between; }
.card-list li { width: 430px; margin-bottom: 50px; }
.card-list li img { width: 100%; }

.more { width: 70px; margin: auto; text-align: center; cursor: pointer; }
.more i { font-size: 50px; }
.more span { display: block; margin-top: -20px; font-size: 16px; }
